.page {
  font-family: "Poppins", sans-serif;
  color: $main-text-color;
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }
  &:has(.page__products:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
    background-color: $main-bg-color;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &__products {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
